import React from 'react'
import './style.css'

function Input({ type = 'text', label, placeholder, ...otherProps}) {
  let strLabel = label
  strLabel = strLabel.replace(/\s+/g, '-').toLowerCase()

  return (
    <div className="tur-wraper-input">
      <label htmlFor={strLabel}>{label}</label>
      <input type={type} id={strLabel} placeholder={placeholder} />
    </div>
  )
}

export default Input
